@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Nanum+Gothic:wght@400;700&family=Noto+Sans+KR:wght@100..900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 100%;
  height: 100vh;
  font-family: "Noto Sans KR", serif;
}
.darkLight-container {
  width: 100%;
  height: 100px;
  position: fixed;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
}
.darkLight-btn {
  width: 60px;
  height: 60px;
  background-color: rgb(33, 63, 92);
  margin-right: 30px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.darkLight-btn-Icon {
  font-size: 30px;
  color: white;
}
/* -------------------------- 메인 화면 ------------------------- */
.main-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
}
.main-circle {
  width: 350px;
  height: 350px;
  border-radius: 100%;
  background: linear-gradient(to top, #a0a7ca, #fcfafe);
  position: absolute;
  left: 200px;
  top: 120px;
  z-index: -1;
}

.main-text {
  width: 55%;
  font-family: "Black Han Sans", serif;
  text-align: center;
}

.main-name {
  font-size: 100px;
  margin-bottom: 30px;
  color: rgb(127, 142, 240);
}
.main-name span {
  font-size: 120px;
}
.main-front {
  font-size: 60px;
  color: rgb(127, 142, 240);
}

.main-portfolio {
  font-size: 60px;
  color: rgb(127, 142, 240);
}
.circle-box {
  width: 100%;
  height: 100px;
  position: absolute;
  bottom: 0;
}


/* -------------------------- 자기소개 화면 ------------------------- */
.about-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.about-me {
  color: rgb(191, 198, 244);
  position: absolute;
  left: 30px;
  font-size: 70px;
  font-family: "Black Han Sans", serif;
  writing-mode: vertical-lr;
  letter-spacing: 5px;
}

.about-intro {
  width: 90%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 20px;
}

.about-intro-img {
  width: 300px;
  height: 300px;
  background-color: rgb(127, 142, 240);
  margin-right: 80px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-intro-img img {
  width: 100%;
}

.about-intro-text {
  font-size: 20px;
}

.about-intro-text p {
  font-size: 17px;
  margin-bottom: 5px;
  display: flex;
  align-items: center;

}

.about-intro-name {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 15px;
}

.about-intro-name p:first-child {
  font-size: 40px;
  margin-right: 10px;
  font-weight: 800;
}

.about-intro-name p:nth-child(2) {
  font-size: 23px;
  color: lightgrey;
}

.about-intro-skill {
  width: 330px;
  margin-top: 25px;
  position: relative;
  display: flex;
  justify-content: space-between;
}

.about-mouse {
  font-size: 20px;
  transform: rotate(80deg);
  animation: mouseAni 1s ease-in-out infinite;
  position: absolute;
}

@keyframes mouseAni {
  0% {
    opacity: 1;
    bottom: 0px;
    left: 0px;
  }

  50% {
    opacity: 1;
    bottom: 5px;
    left: 10px;
  }

  100% {
    opacity: 1;
    bottom: 0px;
    left: 0px;
  }

}

.about-intro-skill-img {
  cursor: pointer;
}
.about-intro-skill-text {
  width: 380px;
  height: 150px;
  position: absolute;
  background-color: rgb(127, 142, 240);
  color: white;
  border-radius: 20px;
  margin-left: 45px;
  margin-top: 60px;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}

.about-intro-skill-text p {
  width: 100%;
  height: 100%;
  text-align: center;
}

.about-text {
  width: 90%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-text-div {
  width: 80%;
}
.about-text-text {
  font-size: 18px;
  letter-spacing: 1px;
}
.about-text-text strong {
  font-size: 19px;
}


/* -------------------------- 프로젝트 소개 ------------------------- */
.project-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.project-sideText {
  color: rgb(191, 198, 244);
  position: absolute;
  left: 30px;
  font-size: 70px;
  font-family: "Black Han Sans", serif;
  writing-mode: vertical-lr;
  letter-spacing: 5px;
}

.project {
  width: 80%;
  height: 80%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.project img {
  width: 500px;
  cursor: pointer;
  margin-right: 10px;
}
.project-imgBox {
  width: 50%;
  height: 80%;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}
.project-textBox {
  width: 50%;
  height: 80%;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}
.project-text {
  width: 100%;
}
.project-text-common{
  font-size: 17px;
  margin-bottom: 5px;
}
.project-text-common span {
  color: rgb(127, 142, 240);
  font-weight: 800;
  margin-right: 10px;
}
.project-about-div {
  padding: 20px;
  margin-top: 20px;
  border-radius: 20px;
  background-color: #F4F4F4;
}
.project-about {
  line-height: 30px;
}
.project-imgBox p {
  font-size: 30px;
  font-weight: 700;  
}
.project-imgBox-number {
  font-size: 35px;
  color: rgb(127, 142, 240);
  font-family: "Black Han Sans", serif;
  margin-right: 15px;
}
.project-link {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.project-link-icon {
  color: black;
  margin-right: 20px;
}
.project-link-icon:hover {
  color: rgb(127, 142, 240);
}

/* -------------------------- footer ------------------------- */
.footer-container {
  width: 100%;
  height: 60px;
  background-color: rgb(127, 142, 240);
  display: flex;
  justify-content: center;
  align-items: center;
}
